html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button,
button:active,
button:focus {
  background-color: transparent;
  padding: 1vh 0vh;
  font-weight: bold;
  font-size: 5vmax;
  text-decoration: none;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer;
}

input,
input:active,
input:focus {
  border: none;
  outline: none;
}

::-webkit-input-placeholder { /* WebKit, Blink, Edge */
  color:    #d1d1d1;
}
::-ms-input-placeholder { /* Microsoft Edge */
 color:    #d1d1d1;
}

::placeholder { /* Most modern browsers support this now. */
 color:    #d1d1d1;
}

.page {
  height: 100vh;
  width: 100vw;
  display: grid;
  align-content: center;
  justify-content: center;
}

.background {
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 1;
}

.playerForm,
.menu {
  display: grid;
  height: 100vh;
  justify-content: center;
  justify-items: center;
  align-content: center;
  position: relative;
}

.menu>* {
  position: relative;
  z-index: 2;
}

.playerForm>* {
  background-color: transparent;
  text-align: center;
  margin: 1vh;
  color: white;
  text-shadow: 0.2vw 0.2vw gray;
  font-weight: bold;
  font-size: 6vmax;
  text-decoration: none;
  border: none;
  text-transform: uppercase;
  position: relative;
  z-index: 2;
}

.lifeInput {
  font-size: 10vmax;
  width: 20vmax;
}

.nameInput {
  background-color: white;
  color: black;
  text-shadow: none;
  width: 40vmax;
  border-radius: 0.5vmax;
  box-shadow: inset 0.2vw 0.2vw 0.2vw gray;
}
.board {
  background-color: gray;
  display: grid;
  height: 100vh;
  width: 100vw;
  grid-template-columns: repeat(auto-fit, minmax(20vw, 1fr));
  grid-auto-rows: minmax(25vw, 1fr);
  align-items: stretch;
  color: white;
  text-shadow: 0.2vw 0.2vw gray;
  font-weight: bold;
}

.player_card {
  display: grid;
  align-content: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.player_info {
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: 1fr 1fr;
  text-align: center;
}

.player_life {
  font-size: 10vw;
}

.player_name {
  font-size: 3vw;
  font-style: italic;
  padding: 0.1vw;
}
.dashboard {
  grid-auto-flow: column;
  display: grid;
  align-content: center;
  justify-content: center;
  height: 100vh;
}
